.Testimonial_cart {
  width: 100%;
  height: 264px;
  border: 1px solid #e1e1e1;
}

.Testimonial_cart_inner {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0px 25px;
}

.Testimonial_cart_parant img {
  width: 55px !important;
  height: 55px !important;
  border-radius: 50%;
  margin: 0px !important;
}

.Testimonial_cart_parant {
  display: flex;
  align-items: center;
  justify-content: left;
  margin: 14px 0px 17px 0px;
  padding: 27px 0px 0px 0px;
}

.Testimonial_cart_right {
  color: #777777;
  font-weight: 400;
  margin-left: 11px;
  line-height: 1.3;
}

.Testimonial_cart_right p {
  margin: 0px !important;
  font-size: 13px;
}

.Testimonial_cart_right h6 {
  display: block;
  margin-bottom: 2px;
  font-size: 16px;
  font-weight: 700;
  color: #222529;
}

.fa-solid.fa-quote-left {
  color: #222529;
  font-weight: 900;
  font-size: 23px;
  margin-top: -4px;
  margin-left: 4px;
}

.Testimonial_cart_disc {
  display: flex;
  margin-top: 7px;
}

.Testimonial_cart_disc p {
  font-weight: 400;
  letter-spacing: 0.013px;
  line-height: 24px;
  font-size: 13px;
  color: #777777;
  margin-left: 12px;
  margin-bottom: 20px;
}

:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-dots-bottom {
	bottom: 55px !important;
	left: 0% !important;
}
