.sidebar_post_carousel {
  /* width: 100%;
      height: 410px; */
}
.sidebar_post_slide {
  border: 1px solid #e7e7e7 !important;
  padding: 0px 0px 42px 0px;
}
.sidebar_post_slide .sidebar_post_inner {
  display: flex;
  flex-direction: column;
}
.sidebar_post_slide .sidebar_post_inner .sidebar_post_left {
  position: relative;
  margin: 0px 0px 0px 0px;
}
.sidebar_post_slide .sidebar_post_inner .sidebar_post_left .sidebar_post_date {
  background-color: #222529;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px;
  left: 10px;
  padding: 0px 5px 5px 5px;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_left
  .sidebar_post_date
  span {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0.7px;
  text-align: center;
  color: #ffffff;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_left
  .sidebar_post_date
  p {
  font-size: 11.2px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 8px;
  letter-spacing: 0.7px;
  text-align: center;
  color: #ffffff;
  margin-top: 0px;
}
.sidebar_post_slide .sidebar_post_inner .sidebar_post_right {
  display: flex;
  flex-direction: column;
  padding: 15px 15px;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_left
  .sidebar_post_image_wrapper {
  overflow: hidden;
}
.sidebar_post_slide .sidebar_post_inner img {
  width: 100% !important;
  height: 210px !important;
  margin-bottom: 0px !important;
  transition: 1s !important;
}
.sidebar_post_slide .sidebar_post_inner img:hover {
  transform: scale(1.1) !important;
  opacity: 0.8 !important;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_right
  .sidebar_post_design_trends {
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 0.01em;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  background-color: #222529;
  padding: 8px 13px;
  width: fit-content;
  margin: 5px 0px 13px;
  border-radius: 5px;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_right
  .sidebar_post_title {
  text-decoration: none;
  color: #222529;
  font-size: 19px;
  margin-bottom: 11px;
  font-size: 18px;
  font-weight: 700;
  line-height: 27px;
  letter-spacing: -0.9px;
}
.sidebar_post_slide
  .sidebar_post_inner
  .sidebar_post_right
  .sidebar_post_content {
  font-size: 13px;
  line-height: 1.923;
  letter-spacing: 0.1px;
  color: #777777;
  margin: 0;
  margin-bottom: 8px;
}
.sidebar_post_slide .sidebar_post_inner .sidebar_post_right a {
  font-size: 10px;
  color: #777777;
  font-weight: 300;
}
.sidebar_post_slide
  :where(.css-dev-only-do-not-override-161f05s).ant-carousel
  .slick-dots-bottom {
  bottom: 50px !important;
  left: -72% !important;
}
