.popular_category {
  margin-top: 35px;
}

.popular_category_down_inner {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.popular_category_item_inner {
  padding: 20px;
  display: flex;
  justify-content: space-between;
}

.popular_category_item_content {
  width: 111%;
  line-height: 30px;
}

.popular_category_item {
	border: 1px solid rgba(34, 34, 34, 0.1);
	border-radius: 10px;
	width: 32%;
	height: auto;
	background: #f0f1f3;
}

.popular_category_upper {
  margin: 0px 0px 30px 0px;
}

.popular_category .popular_category_upper h1 {
  font-family: "Poppins", sans-serif;
  color: #222529;
  font-family: "poppins", sans-serif;
  text-wrap: nowrap;
  font-weight: 700;
  line-height: 1;
  font-size: 24px;
  text-align: center;
  -webkit-text-decoration: underline 3px solid black;
  text-decoration: underline 3px solid #222529;
}

.popular_category_item_content span {
  background-color: #3cb371;
  border-radius: 50px;
  font-size: 12px;
  font-weight: 500;
  padding: 4px 12px;
  border-radius: 25px;
  color: white;
}

.popular_category_item_content h6 {
  margin-top: 11px;
  font-size: 14px;
  margin: 0px 0px;
  padding-left: 3px;
}

.popular_category_item_content p {
  /* color: #333333; */
  font-size: 14px;
  font-weight: 500;
  margin-top: 9px;
  line-height: 1;
  margin: 0px 0px 27px 0px;
  padding-left: 3px;
}

.popular_category_item_img {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.popular_category_item_img img {
	width: 60%;
	height: auto;
	margin-right: 30px;
	border-radius: 35%;
}

.popular_category_item_content a {
	text-decoration: none;
	color: black;
	background-color: white;
	font-family: "Catamaran", sans-serif;
	border-radius: 50px;
	padding: 2px 12px;
	font-size: 15px;
	letter-spacing: 0.2px;
	border: 2px solid rgba(34, 34, 34, 0.1);
	transition: 300ms;
}

.popular_category_item_content a:hover {
  background: #2460e9;
  color: white;
}
