body {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}

.container {
  margin: 0 10px;
  padding: 0px;
  display: block;
  box-sizing: border-box;
  /* max-width: 1247px; */
  /* width: 1337px; */
  width: 1329px;
}

/* promo---------start */

.promo_section {
  background-color: #c5fff1;
  /* background-color: #232f3f; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-bottom: 1px solid #636363;
}

.promo_section .promo_section_inner {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 13px;
  padding: 3px 0px;
}

.promo_section .promo_section_inner .promo_section_sub_heading p {
  font-weight: 500;
  letter-spacing: 0.325px;
  line-height: 2.2;
  color: #000;
  /* color: #fff; */
  font-size: 13px;
  margin: 0;
  animation: colorBlink 2s steps(1, start) infinite; /* steps(1) ensures immediate color changes */
}

/* Keyframes for colorBlink */
@keyframes colorBlink {
  0% {
    color: #000; /* Start with black */
  }
  50% {
    /* color: #ff5500;  */
    color: #ee3131; /* Change directly to red */
    color: red; 
  }
  100% {
    color: #000; /* Change directly back to black */
  }
}


.promo_section .promo_section_inner .fa-solid.fa-rectangle-ad {
  color: #f50;
  margin-right: -5px;
  font-size: 16px;
}

.promo_section .promo_section_inner .promo_section_sub_heading p span {
  font-weight: 700;
}

.promo_section_men_women {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.promo_section .promo_section_inner .promo_section_men_women a {
  font-size: 0.625rem;
  color: #ffffff;
  line-height: 1;
  font-weight: 700;
  letter-spacing: 0.025em;
  padding: 8px 11px 8px 11px;
  background-color: #151719;
  text-decoration: none;
  margin-right: 3px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.promo_section .promo_section_inner .promo_section_men_women a:hover {
  color: rgb(228, 221, 221);
  background-color: #ee3131;
}
.promo_section .promo_section_inner .promo_section_condition {
  font-size: 11px;
  color: white;
  line-height: 1;
  letter-spacing: 0.325px;
  margin-left: -6px;
}

.promo_section .promo_section_inner .promo_section_condition p {
  margin-bottom: 0px;
  color: #333e48;
  /* color:white; */
  font-weight:500;
  animation: colorBlink 2s steps(1, start) infinite;
}

.promo_section .promo_section_inner .fa-solid.fa-xmark {
  content: " /f00d";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  position: absolute;
  color: #000;
  right: 1%;
  font-size: 22px;
  cursor: pointer;
  position: fixed;
}

.promo_section .container i.fa-solid.fa-xmark {
	position: absolute;
	top: 7px;
	right: 10px;
	font-size: 24px;
	cursor: pointer;
  color: #333e48;
}

.promo_section {
  position: relative;
  overflow: hidden;
  width: 100%;
}

.promo_section_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation: slide 40s linear infinite;
}

@keyframes slide {
  0% {
    transform: translateX(100%); /* Start from right */
  }

  100% {
    transform: translateX(-100%); /* Return to the right */
  }
}


.promo_section i {
  font-size: 24px;
  cursor: pointer;
}

.promo_section_sub_heading p span {
  font-weight: bold;
}

.promo_section_men_women a {
  margin: 0 10px;
  text-decoration: none;
}


