.flash_deal {
  border: 3px solid #ee3131;
  width: 100%;
  overflow: hidden;
  height: auto;
}

.flash_deal_inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sec1 {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}

.sec1 img {
  scale: 1;
  margin-bottom: 8px;
  margin-top: 10px;
}

.sec1 p {
}

.sec1 p:nth-child(4) {
	display: block;
	padding: 0px 6px 0px 6px;
	font-size: 16px;
	font-weight: 700;
	background: #222529;
	color: #fff;
	margin: 0;
	position: absolute;
	top: 11%;
	right: 4%;
}

.sec1 p:nth-child(3) {
	display: block;
	padding: 0px 9px 5px 9px;
	font-size: 16px;
	font-weight: 700;
	background: #f4631b;
	color: #fff;
	margin: 0;
	position: absolute;
	top: 0%;
	right: 4%;
}

.sec2 span {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2px;
}

.flash_deal_part {
  position: relative;
  text-align: center;
}

.flash_deal_inner .sec1 h2 {
  margin-bottom: 0px;
  position: absolute;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.2px;
  text-align: center;
  font-size: 16px;
  color: #222529;
  top: 9%;
  left: 35%;
}

.flash_deal_part a {
  text-decoration: none;
  color: #222529;
  text-align: center;
}

.flash_deal_part a:hover {
  text-decoration: none;
  color: #fa6a31;
}

.flash_deal_part i {
  font-size: 12px;
  font-weight: 600;
  /* color: #222527; */
  color: darkorange;
}

.flash_deal_part.sec3 p {
  display: inline-block;
  color: #a7a7a7;
  font-size: 14px;
  margin-right: 0.2143em;
  vertical-align: baseline;
  text-decoration: line-through;
  margin-top: 10px;
}

.flash_deal_part p:nth-child(2) {
  line-height: 1;
  font-weight: 600;
  color: #444;
  font-size: 18px;
  text-decoration: none;
  /* margin-bottom: 13px; */
  margin: 10px 0px 10px 0px;
}

.flash_deal_part.sec4 {
  display: inline-block;
  width: auto;
  padding: 12px 35px 12px 35px;
  color: #444;
  background: #f4f4f4;
  border-radius: 2rem;
  font-family: "Oswald";
  /* text-align: center; */
  margin-bottom: 9px;
}

#demo {
  display: inline-block;
  width: auto;
  /* padding: 11px 12px 12px 21px; */
  color: #444;
  background: #f4f4f4;
  border-radius: 2rem;
  margin: 0;
  font-size: 14px;
  font-weight: 400;
  word-spacing: 3px;
  font-family: Poppins, sans-serif;
}

.flash_deal_part.sec4 span {
  font-size: 11px;
  font-weight: 400;
  color: #222529;
  text-transform: uppercase;
  letter-spacing: -0.01em;
  margin: 0;
}
