/* include parent class selectors for each child class */
* {
  margin: 0;
  padding: 0;
  font-family: "Catamaran", sans-serif;
  font-family: "Poppins", serif;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 10px;
  padding: 0px;
  display: block;
  box-sizing: border-box;
  /* max-width: 1247px; */
  /* width: 1337px; */
  max-width: 1329px;
}
.container2 {
  margin: 0 auto;
  padding: 0px;
  display: block;
  box-sizing: border-box;
  /* max-width: 1247px; */
  /* width: 1337px; */
  max-width: 100%;
}

/* Home-css--start */

.home_section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}

.home_section_sidebar {
	width: 23%;
	padding: 0px 10px 0px 20px;
	gap: 15px;
	display: flex;
	flex-direction: column;
}

.home_section_main_sections {
  width: 77%;
  display: flex;
  flex-direction: column;
  padding: 0px 20px 0px 10px;
  gap:25px;
}
/* Home-css--End*/

/* home_Category_section_start */

.category_container {
	display: flex;
	flex-wrap: nowrap;
	/* overflow-x: auto; */
	/* gap: 10px; */
	/* margin: 0px 18px; */
	display: flex;
}

/* .category_section {
	flex: 1 1;
	min-width: 100px;
	padding: 15px 0px 0px 0px;
} */

.category_section {
	flex: 1 1;
	/* min-width: 100px; */
	padding: 0px 0px 0px 0px;
}

.category_section_inner {
  text-align: center;
}

.category_section_inner a {
  text-decoration: none;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.category_section_image_and_content_category {
	width: 90px;
	height: 90px;
	border-radius: 50%;
	/* background-color: #ee3131; */
	display: flex;
	align-items: center;
	align-items: center;
	justify-content: center;
}

.category_section_image_and_content img {
	width: 50px;
	height: 50px;
	border-radius: 50%;
}

.category_section_image_and_content h2 {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: -0.35px;
  line-height: 15px;
  font-size: 13px;
  color: #666666;
  font-family: Poppins, sans-serif;
  margin-top: 9px;
}

.category_container
  .category_section
  .category_section_inner
  .category_section_image_and_content
  .fa-solid.fa-bars {
  font-size: 30px;
  color: white;
}

/* home_Category_section_end */

/* -----Laber-template-start---- */

.laber_template {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px 0px;
}

.laber_template_inner {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 10px 10px;
  border: 1px solid #ebebeb;
  background-color: white;
  border-radius: 3px;
}

.laber_template_inner img {
  scale: 74%;
  margin: 0px -25px;
}

.laber_template_inner img:hover {
  transform: scale(1.1);
  transition: opacity 0.3s, transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* -----Laber-template-Finish---- */

/* -----Laber-template2-Start---- */
.laber_template2 {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0px 0px;
}

.laber_template_inner2 {
	display: flex;
	align-content: center;
	justify-content: center;
	flex: 3 1;
	gap: 10px;
	display: flex;
	flex-direction: column;
}

.laber_template_inner_img2 {
  /* margin: 0px 10px; */
  overflow: hidden;
  border-radius: 15px;
  display: flex;
  position: relative;
}

.laber_template_inner_img2 img {
  border-radius: 15px;
  background-size: contain;
  background-position: left;
  width: 100%;
  height: auto;
  transition: opacity 0.3s, transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.laber_template_inner_img2 img:hover {
  transform: scale(1.1);
  transition: opacity 0.3s, transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94),
    -webkit-transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.laber_template_inner_img2 p {
  position: absolute;
  font-size: 14px;
  color: white;
  font-weight: 600;
  margin: 0;
  left: 28px;
  top: 37%;
  transform: translate(0%, -37%);
}

.laber_template_inner_img2 p:hover img {
  transform: scale(1.1);
  transition: opacity 0.3s, transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.laber_template_inner_img2 p span {
  position: absolute;
  font-size: 11px;
  color: white;
  font-weight: 400;
  margin-top: 5px;
}

:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	touch-action: pan-y;
	-webkit-touch-callout: none;
	-webkit-tap-highlight-color: transparent;
	margin: 0;
}
/* -----Laber-template2-Finish---- */

