.sidepanel_category_menu {
	position: relative;
}

.sidepanel_category_menu_hide{
  display: none;
}

.sidepanel_category_menu_tringle {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 35px;
  border-bottom: 14px solid #e7e7e7;
  border-left: 12px solid transparent;
  border-right: 12px solid transparent;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul {
	list-style: none;
	border: 1px solid #e7e7e7;
	padding: 0;
	display: block;
	margin: 0;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul li {
  list-style: none;
  border-bottom: 1px solid #e7e7e7;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* padding-top: 14px; */
  padding-right: 17px;
  /* padding-bottom: 14px; */
  padding-left: 21px;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul li:hover i {
  color: #ee3131;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul li a {
  text-decoration: none;
  text-decoration: none;
  color: #333e48;
  font-size: 14px;
  font-weight: 600;
  line-height: 55px;
  font-family: "Poppins", serif;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul li:hover a {
  color: #ee3131;
}

.sidepanel_category_menu .sidepanel_category_menu_inner ul li .fa-solid {
  font-size: 18px;
  margin-right: 12px;
  color: #333e48;
}

.sidepanel_category_menu
  .sidepanel_category_menu_inner
  ul
  li
  .fa-solid.fa-chevron-right {
  color: #838b90;
  font-size: 10px;
  margin-right: 8px;
}

.sidepanel_category_menu
  .sidepanel_category_menu_inner
  ul
  li:hover
  .fa-solid.fa-chevron-right {
  color: #ee3131;
  font-size: 10px;
  margin-right: 8px;
}
