:where(.css-dev-only-do-not-override-161f05s).ant-carousel {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: rgba(0, 0, 0, 0.88);
  font-size: 14px;
  line-height: 1.5714285714285714;
  list-style: none;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  width: 100%;
}

.hero_section {
  /* margin: 80px 0px; */
  width: 100% !important;
  display: inline-block !important;
  height: 100% !important;
  padding-bottom: 15px;
}

.hero_section .container {
  max-width: 100%;
  margin: 0 auto;
  /* padding: 0 20px; */
  box-sizing: border-box;
  display: block;
}

.hero_section_left {
  position: relative;
  width: 40%;
  height: 100%;
}

.hero_section_right {
	padding-top: 40px;
	width: 60%;
	height: auto;
	display: flex;
	align-items: center;
	justify-content: center;
}

.hero_section_left.hero_section_left2 {
  position: relative;
  width: 40%;
  height: 100%;
}

.hero_section_right.hero_section_right2 {
  padding-top: 48px;
  width: 60%;
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero_section .hero_section_inner {
  background-image: url("/public/images/shop50-slider-bg-1.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 428px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  border-radius: 10px;
}

.hero_section_left img {
  width: auto;
  height: 171px;
  margin: 55px 0px 0px 53px;
  opacity: 0;
  animation: fadeInMove 0.5s ease-in-out 0.3s forwards;
}
.hero_section:hover .hero_section_left img {
  opacity: 1;
  animation: fadeInMove 0.5s ease-in-out 0.3s forwards;
}

.hero_section .hero_section_inner .hero_section_right img {
	width: 83%;
	height: 403px;
}

.hero_section .hero_section_inner .hero_section_right2 img {
  width: 77%;
  height: 393px;
}

.hero_section_main_heading h2 {
  font-weight: 700;
  line-height: 1;
  font-size: 40px;
  color: #222529;
  font-family: "poppins", sans-serif;
  text-align: left;
