  top: 130px;
  text-wrap: nowrap;
  left: 95px;
  font-weight: 700;
  line-height: 1;
  font-size: 3em;
  opacity: 0;
  animation: fadeInMoveHeading 0.5s ease-in-out 0.5s forwards;
