@keyframes fadeInMove {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInMove2 {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInMoveHeading {
  0% {
    opacity: 0;
    top: 130px;
  }
  100% {
    opacity: 1;
    top: 104px;
  }
}

@keyframes fadeInMoveHeading2 {
  0% {
