.slick-prev,
.slick-next {
  z-index: 2;
  display: block !important;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  height: 40px;
  width: 10px;
}

.slick-prev:hover,
.slick-next:hover {
  background-color: #944cf5;
}

.slick-prev {
  left: 0px; /* Adjust left positioning */
}

.slick-next {
  right: -100px; /* Adjust right positioning */
}

:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-prev {
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  transform: translateY(-50%);
  color: #000;
  opacity: 1;
  background: white;
  padding: 0;
  line-height: 0;
  /* border: 0; */
  outline: none;
  cursor: pointer;
  z-index: 1;
  transition: opacity 0.3s;
  display: flex !important;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid #e7e7e7;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.03);
  /* right: 1.5%; */
  left: -1.5%;
}

:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-next {
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  transform: translateY(-50%);
  color: #000;
  opacity: 1;
  background: white;
  padding: 0;
  line-height: 0;
  border: 0;
  outline: none;
  cursor: pointer;
  z-index: 1;
  transition: opacity 0.3s;
  display: flex !important;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 1px solid #e7e7e7;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.03);
  right: -1.5%;
}

:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-prev:hover,
:where(.css-dev-only-do-not-override-161f05s).ant-carousel .slick-next:hover {
  border: 1px solid #944cf5;
  background: #944cf5;
  transition: background 0.3s, border 0.3s, color 0.3s;
  color: white;
}

