@keyframes pulse-glow {
  0% {
    box-shadow: 0 0 0 0 rgba(42, 42, 42, 0.7);
  }

  50% {
    box-shadow: 0 0 20px 10px rgba(42, 42, 42, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(42, 42, 42, 0.7);
  }
}

.add-to-cart {
  font-family: "Rubik", serif;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 500;
  font-size: 13px;
  background-color: #2a2a2a;
  color: white;
  border-radius: 30px;
  border: none;
  width: 100%;
  display: flex;
  padding: 0 30px;
  position: relative;
  align-items: center;
  justify-content: center;

  /* No animation by default */
  animation: none;
}

.add-to-cart:hover {
  background-color: #5433eb;
  border-color: #16bcdc;
  color: #ffffff;
  animation: pulse-glow 1s ease 0s infinite, pause 4s ease 1s infinite;
}

@keyframes pause {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 1;
  }
}

.group-button .add-to-cart::before,
.btn.product-affiliate::before {
  content: "";
  top: 0;
  left: 0;
  z-index: 1;
  background: linear-gradient(
    to right,
    #fff0 0,
    #ffffff08 1%,
    #fff9 30%,
    #ffffffd9 50% 70%,
    #ffffffd9 71%,
    #fff0
  );
  height: 100%;
  transform: skew(-10deg);
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  position: absolute;
  right: 0;
  bottom: 0;
  opacity: 0.15;
}

