.main-footer {
    position: relative;
    z-index: 0;
    background-color: #232f3f;
    /* background-color: #292a36; */
    font-family: "Poppins";
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}



.footer-top {
    border-top: 1px solid #343540;
}

.footer-inner {
    width: 100%;
    height: auto;
    color: #ccc;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer-top-inner {
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0px;
    /* align-items: center;
    justify-content: center; */
}

.footer-top-inner-left {
    /* padding: 0px 12px; */
    display: flex;
    align-items: center;
}

.footer-top-inner-right {
    /* padding: 0px 12px; */
    display: flex;
    align-items: center;
    justify-content: center;
}


.signUp-title {
    display: inline-block;
    vertical-align: top;
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
    line-height: 42px;
    padding-right: 20px;
}

.footer-top-inner p {
    display: inline-block;
    /* vertical-align: top; */
    color: #fff;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
    line-height: 42px;
    padding-right: 20px;
}

.subscribe-email-div {


    border-radius: 10px;
    display: flex;
    position: relative;

}

.email-box {
    height: 42px;
    border: 1px solid #e5e5e5;
    line-height: 1.66;
    background-color: #fff;
    width: 240px;
    padding: 0px 7px;
    box-sizing: border-box;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;

}

.email-box::placeholder {
    color: black;
}

.subscribe-email-div,
.email-box:focus {
    outline: none;

}

.subscribe-email-button {
    width: 100px;
    height: 42px;
    border-radius: 0px 3px 3px 0px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    /* padding: 0px 30px; */
    /* font-weight: 600; */
    color: white;
    /* outline: none; */
    border: none;
    font-size: 12px;
    cursor: pointer;
    /* position: absolute; */
    /* right: 128px; */
    text-transform: uppercase;
    font-family: "Poppins";
    right: 0px;
    z-index: 0;
    /* background-color: #7b68ee; */
    background-color: #ff5c00;
}

.subscribe-email-button:hover {
    background-color: #cc4a00;

}









.footer-middle {
    width: 100%;
    padding: 57px 0px;
    border-top: 1px solid rgb(255, 255, 255, 0.2);
    border-top: 1px solid #343540;
}

.footer-middle-inner {
    /* width: 100%; */
    /* height: auto; */
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
}

.col {
    /* flex: 4; */

}

.col p:before {
    background-color: #ff5c00;
}

.col p:before {
    content: '';
    display: block;
    width: 40px;
    height: 2px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.col a {
    color: white;
    font-weight: 500;
}

.app_download_image {
    display: flex;
    gap: 10px;
    /* align-items: center; */
    justify-content: flex-end;

}

.app_download_image img {
    scale: 90%;
    margin: 0px 0px 0px -5px;
}


.col1 a {
    width: fit-content;
}

.col1 {
    width: 264px;
    /* height: 236px; */
    /* padding: 0px 12px; */
