.col1 .social-div:hover {
    background-color: #7b68ee;

    transition: ease-in-out 0.4s;
}

.shop_more {
    background-color: #ff5c00;
    text-transform: uppercase;
    padding: 0 10px;
    display: table;
    margin-bottom: 30px;
    border-radius: 3px;
    text-decoration: none;
    font-size: 12px;
}

.shop_more:hover{
    background-color: #cc4a00;
}



