.cart1 {
  margin: 0px 0px;
}

.cart1_inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.cart1_inner {
  display: flex;
  justify-content: space-between;
}

.cart1_item {
  width: 19%;
  border: 2px solid #ee3131;
  border: 1px solid rgba(34, 34, 34, 0.1);
  border-radius: 5px;
  box-shadow: rgba(17, 17, 26, 0.05) 0px 4px 16px,
    rgba(17, 17, 26, 0.05) 0px 8px 32px;
}

.cart1_item_inner {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  flex-direction: column;
  padding: 15px;
}

.cart1_item_inner img {
  width: 100%;
  /* position: relative; */
}

.cart1_item_tag {
  /* position: absolute; */
  background-color: #cc0c39;
  color: white !important;
  font-size: 11px !important;
  width: fit-content !important;
  padding: 2px 11px !important;
  font-size: 10px !important;
  margin: 0px !important;
  font-weight: 500;
}

.cart1_item_inner a {
  text-decoration: none;
  color: #0068c9;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  margin: 5px 0px 0px 0px;
  display: flex;
  gap: 4px;
}

.cart1_item_inner a:hover {
  color: #ee3131;
}

.cart1_item_inner .cart1_item_product_price {
  text-decoration: unset;
  line-height: 22px;
  font-weight: 600;
  margin: 0px;
  margin-top: 5px;
  font-size: 14px;
}

.cart1_item_inner .cart1_item_product_price span {
  font-size: 13px;
  color: #565959;
  font-weight: 400;
}

.cart1_item_inner .cart1_item_product_price span.cart1_item_discount {
  font-size: 13px;
  color: #565959;
  font-weight: 400;
  text-decoration-line: line-through;
}

.cart1_item_actions {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart1_item_inner .cart1_item_buy_now {
  text-decoration: none;
  color: #0068c9;
  font-size: 14px;
  line-height: 10px;
  font-weight: 500;
  background-color: #ef233c !important;
  color: white;
  padding: 10px 25px;
  border-radius: 50px;
  width: fit-content;
  margin: 0px 0px 0px 0px;
}

.cart1_item_inner .cart11_item_buy_now:hover {
  color: white;
}

.cart1_item_inner span.kohli .fa.fa-star.checked {
  /* color: #ffb800; */
  color: #ef233c;
}

.cart1_item_inner .fa-heart {
  color: #cc0c39;
  font-size: 30px !important;
}

.fa-solid.fa-bookmark {
  color: #cc0c39;
}

:where(.css-dev-only-do-not-override-161f05s).ant-rate
  .ant-rate-star:not(:last-child) {
  margin-inline-end: 3px;
}

:where(.css-dev-only-do-not-override-161f05s).ant-rate {
  color: #ef233c !important;
}

svg {
  /* color: #fadb14; */
}

.anticon {
  font-size: 16px;
}

.cart1_rating_count {
  text-decoration: unset;
  line-height: 22px;
  font-weight: 600;
  margin: 0px;
  margin-top: 0px;
  margin-top: 5px;
  font-size: 14px;
  font-size: 13px;
  color: #565959;
  font-weight: 400;
  margin-left: 4px;
}

.cart1 .cart1_inner .cart1_item .cart1_item_inner .cart-button {
  transform: translateY(20px);
  visibility: visible;
}

.cart1 .cart1_inner .cart1_item .cart1_item_inner img:hover .cart1-button {
  transform: translateY(0px);
  visibility: visible;
  transition: 0.5s;
}

/* Cart1--start */
.display-header.d-flex.align-items-center.justify-content-between {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cart-button.d-flex.gap-2.justify-content-center.align-items-center {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  gap: 10px;
  left: 44px;
  top: 62px;
  margin: 0 auto;
}

.row.row-cols-1.row-cols-sm-2.row-cols-md-3.row-cols-lg-5 {
  display: flex;
  align-items: baseline;
  gap: 0px 10px;
}

.d-inline-block.text-uppercase.text-hover.fw-bold {
  color: black;
  text-decoration: none;
  font-weight: 600;
}

.section-title.text-uppercase {
  margin: 0;
}

.product-card img {
  width: 100%;
}

.product-content .product-card:hover .cart-concern {
  bottom: 85px;
  opacity: 1;
}

.section-title {
  font-size: 1.4rem;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #222529;
}

.btn-right {
  text-align: right;
  display: block;
}

.text-hover ul li > a,
.text-hover a,
a.text-hover {
  position: relative;
  transition: 0.3s ease-out;
}

.text-hover li a.nav-link::after,
.text-hover li a::after,
.text-hover a::after,
a.text-hover::after {
  content: "";
  width: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: 0.3s ease-out;
  border-bottom: 2px solid var(--dark-color);
}

.product-content.padding-small {
  padding: 15px 0px;
}

.product-content .product-card .cart-concern {
  right: 50%;
  bottom: 70px;
  left: 50%;
  opacity: 0;
  z-index: 9;
  border-radius: 10px;
  transition: 0.3s ease-out;
}

.cart1-button .btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  line-height: 1;
  transition: transform 0.3s ease-out;
}

.card-img {
  position: relative;
}

.cart-concern.position-absolute.d-flex.justify-content-center {
  position: absolute;
}

.product-card .cart-button svg {
  width: 20px;
  height: 20px;
  fill: black;
}

.fa-cart-shopping {
  content: "\f07a";
  font-family: "font awesome 6 free";
  font-weight: 900;
  color: black;
  font-size: 18px;
}

.cart-button .btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  line-height: 1;
  transition: transform 0.3s ease-out;
  background-color: white;
  border: 0px;
}

a {
  color: black;
  text-decoration: none;
  transition: 0.3s color ease-out;
}

.col a {
  color: #333e48;
  font-weight: 500;
  font-size: 15px;
  display: flex;
  gap: 6px;
  align-items: center;
  justify-content: center;
}

.col.mb-4 {
  border: 1px solid #e7e7e7;
}

/* Cart1--end */

/* ---cart3---start-- */

.cart3 .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin: 0;
}

.cart3 .row .product {
  width: 24%;
  border: 2px solid #e7e7e7;
  position: relative;
  padding: 0px;
}

.cart3 .row .product:hover {
  border: 2px solid #ef233c;
  transition: 0.2s;
}

.cart3 .row .product:nth-child(n) {
  margin-bottom: 22px;
}


.cart3 .row .product:hover .add-to-cart {
  transform: translateY(0px);
  transition: 0.2s;
}

.cart3 .row .product .availabilityStatus {
  background: #45c173;
  font-size: 12px;
  /* text-transform: lowercase; */
  color: #fff;
  padding: 2px 4px;
  position: absolute;
  top: 15px;
  left: 15px;
  z-index: 2;
}

.cart3 .row .products-tabs > .tab-pane.active {
  opacity: 1;
  visibility: visible;
  height: auto;
}

.cart3 .row .products-tabs > .tab-pane {
  display: block;
  height: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: hidden;
  padding-bottom: 60px;
  margin-bottom: -60px;
}

.cart3 .row .product .product-img {
  width: 78%;
  margin: 30px auto 0px;
}

.cart3 .row .product .product-img img {
  width: 100%;
}


.cart3 .row .product .product-img .product-label {
  position: absolute;
  top: 15px;
  right: 15px;
}

.cart3 .row .product .product-img .product-label > span.new {
  background-color: #45c173;
  border-color: #45c173;
  color: #fff;
}

.cart3 .row .product .product-img .product-label > span {
  border: 2px solid #ff5500;
  padding: 2px 5px;
  font-size: 12px;
  color: #ef233c;
  /* font-weight: 600; */
  position: relative;
  z-index: 2;
}

.cart3 .row .product .product-body {
  position: relative;
  padding: 0px 10px 5px 10px;
  background-color: #fff;
  text-align: center;
  z-index: 20;
}

.cart3 .row .product .product-body .product-category {
  text-transform: capitalize;
  font-size: 12px;
  color: #8d99ae;
  margin: 0;
  font-size: 10px;
}

.cart3 .row .product .product-body .product-name {
  text-transform: uppercase;
  font-size: 14px;
  color: #2b2d42;
  font-weight: 700;
  margin: 0 0 0px;
  /* white-space: nowrap; */
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  line-height: 1;
  margin: 0px 0px 4px 0px;
  /* gap: 9px; */
  line-height: 1.8;
}

.cart3 .row .product .product-body .product-name > a {
  font-weight: 700;
  color: #2b2d42;
  font-weight: 500;
  transition: 0.2s color;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-transform: none;
}

.product-name span {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  font-weight: 300;
  font-size: 12px;
  text-transform: none;
}

.cart3 .row .product .product-body .product-price {
  color: #d10024;
  font-size: 15px;
  color: #2b2d42;
  font-weight: 500;
  margin: 0 0 10px;
}

.cart3 .row .product .product-body .product-price .product-old-price {
  font-size: 77%;
  font-weight: 500;
  color: #8d99ae;
}

.cart3 .row .product .product-body .product-rating {
  position: relative;
  margin: 0px 0px 0px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.cart3 .row .product .product-body .product-rating::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  height: 1px;
  background-color: #e4e7ed;
}

.cart3 .row .product .product-body .product-rating > i.fa-star {
  color: #ef233c;
}

.cart3 .row .product .product-body .product-rating > i {
  position: relative;
  width: 10px;
  margin-right: 0px;
  background: #fff;
  color: #e4e7ed;
  z-index: 10;
  font-size: 12px;
}

.product-btns {
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart3 .row .product .product-body .product-btns > button {
  position: relative;
  width: 40px;
  height: 40px;
  line-height: 40px;
  background: transparent;
  border: none;
  transition: 0.2s all;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.cart3 .row .product .product-body .product-btns > button:hover {
  background-color: #e9e9ed;
  border-radius: 50px;
  color: #ef233c;
}

.cart3 .row .product .add-to-cart {
  position: absolute;
  left: 3px;
  right: 0px;
  bottom: -61px;
  padding: 15px 0px 14px;
  background: #1e1f29;
  text-align: center;
  transform: translateY(-100%);
  transition: 0.2s all;
  z-index: 3;
  border-radius: 0px;
  width: 98%;
}

.cart3 .row .product .add-to-cart .add-to-cart-btn {
  position: relative;
  border: 2px solid transparent;
  height: 35px;
  padding: 0 30px;
  background-color: #ef233c;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 40px;
  transition: 0.2s all;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cart3 .row .product .add-to-cart .add-to-cart-btn > .fa-shopping-cart {
  position: absolute;
  left: 0;
  top: 0;
  color: #d10024;
  opacity: 0;
  visibility: hidden;
  font-size: 16px;
  transform: translateX(25px);
  margin: 0px 10px 0px 0px;
}

.cart3 .row .product .add-to-cart .add-to-cart-btn:hover {
  border: 2px solid #d10024;
  background-color: #fff;
  color: #d10024;
  padding-right: 15px;
  padding-left: 40px;
}

.cart3 .row .product .add-to-cart .add-to-cart-btn:hover > .fa-shopping-cart {
  position: absolute;
  left: 14px;
  top: 0;
  line-height: 27px;
  color: #d10024;
  opacity: 1;
  visibility: visible;
  transform: translateX(0px);
  transition: 0.5s;
}

.cart3 .row .product .product-body .product-btns .fa-heart {
  color: #000;
  font-size: 19px;
}

.cart3 .row .product .product-body .product-btns .fa-solid.fa-heart {
  color: #ee3131;
  font-size: 19px;
}

.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-wishlist:hover
  .fa-heart {
  color: #ee3131;
  font-size: 19px;
}

.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-wishlist:hover
  .fa-heart {
  color: #ee3131;
  font-size: 19px;
}

.cart3 .row .product .product-body .product-btns .tooltipp {
  position: absolute;
  bottom: 68%;
  left: 50%;
  transform: translate(-50%, -15px);
  width: 115px;
  padding: 7px;
  font-size: 10px;
  line-height: 10px;
  background: #1e1f29;
  color: #fff;
  text-transform: uppercase;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s all;
  border-radius: 50px;
}

.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-wishlist:hover
  .tooltipp1,
.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-compare:hover
  .tooltipp2,
.cart3 .row .product .product-body .product-btns .quick-view:hover .tooltipp3 {
  opacity: 1;
  visibility: visible;
  transition: 0.2s all;
  font-weight: 600;
}

.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-wishlist:hover
  .tooltipp::after {
  border-top: 10px solid #ee3131;
  transition: 0s;
}

.cart3
  .row
  .product
  .product-body
  .product-btns
  .add-to-wishlist:hover
  .tooltipp1 {
  background-color: #ee3131;
  transition: 0s;
}

.cart3 .row .product .product-body .product-btns .tooltipp::after {
  content: "";
  position: absolute;
  bottom: -34%;
  left: 50%;
  transform: translate(-50%, 0px);
  z-index: 10;
  opacity: 1;
  visibility: visible;
  transition: 0.2s all;
  border-top: 10px solid #1e1f29;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
}

/* ---cart3---end-- */
